











import {
	mapActions,
	mapState,
	mapGetters,
	mapMutations
} from 'vuex'

import VScroll from '../../components/scroll'

export default {
	components: {
		VScroll
	},
	computed: {
		...mapState('Book', ['FreeReadList']),
	},
	methods: {
		...mapActions('Book', ['BookFallDown', 'BookReader']),
	},
	async onLoad({ id }) {
		await this.BookReader({ id, start: true })
	}
}
